<template>
  <div id="app">
    <Navigation :current="page" @SwitchPages="SwitchPages" />
    <Return
      v-show="Return()"
      @return="
        (i) => {
          this.page = i;
        }
      "
    />
    <MusicIcon
      @player="
        (i) => {
          player = i;
        }
      "
      :current="page"
      v-show="page != 'MusicPlayer'"
    />
    <transition mode="out-in">
      <component
        :player="player"
        @play="play"
        @up="SwitchPages"
        :is="page"
      ></component>
    </transition>
  </div>
</template>

<script>
import data from "./data/data.json";
import Navigation from "./components/Navigation.vue";
import HomePage from "./components/HomePage.vue";
import AboutMe from "./components/AboutMe.vue";
import Applet from "./components/Applet.vue";
import Contact from "./components/Contact.vue";
import Jokes from "./works/Jokes.vue";
import MusicPlayer from "./works/MusicPlayer.vue";
import NoteBook from "./works/NoteBook.vue";
import MessageBoard from "./works/MessageBoard.vue";
import Return from "./components/return.vue";
import SignIn from "./works/SignIn.vue";
import MusicIcon from "./components/MusicIcon.vue";

export default {
  name: "App",
  components: {
    Navigation,
    HomePage,
    AboutMe,
    Applet,
    Contact,
    Jokes,
    MusicPlayer,
    NoteBook,
    MessageBoard,
    Return,
    SignIn,
    MusicIcon,
  },
  data() {
    return {
      player: "",
      page: "HomePage",
    };
  },
  methods: {
    play(i) {
      this.player.currentTime = i[0];
      if (!i[1]) {
        this.player.play();
      }
    },
    SwitchPages(msg) {
      this.page = msg;
    },
    Return() {
      for (const i of data.app) {
        if (this.page == i) {
          return true;
        }
      }
      return false;
    },
  },
};
</script>

<style scoped>
.v-leave-to,
.v-enter {
  opacity: 0;
}
.v-enter-active,
.v-leave-active {
  transition: all 0.2s ease-in-out;
}
</style>
